import React from 'react';

import './App.styles.css';

const App = () => {
	const fileUpload = e => {
		console.log('nåt händer');
		e.preventDefault();
		let file = document.querySelector('#file').files[0];
		console.log('OUTPUT ÄR: App -> file', file);

		let formData = new FormData();
		formData.append('photo', file);
		console.log('OUTPUT ÄR: App -> formData', formData);
		///////////////////////////////
		var myHeaders = new Headers();
		myHeaders.append('Authorization', 'abc123');

		// var requestOptions = {
		// 	method: 'GET',
		// 	headers: myHeaders,
		// 	redirect: 'follow'
		// };
		///////////////////////////////

		fetch('/files', {
			method: 'POST',
			body: formData,
			headers: myHeaders
		})
			.then(resp => resp.json())
			.then(resp => {
				alert(resp.msg);
			})
			.catch(err => console.error(err));
	};

	const uploadToCloud = e => {
		console.log('upload to cloud funktionen körs');
		e.preventDefault();
		let file = document.querySelector('#cloud-file').files[0];
		console.log('OUTPUT ÄR: App -> file', file);

		let formData = new FormData();
		formData.append('photo', file);
		console.log('OUTPUT ÄR: App -> formData', formData);

		var myHeaders = new Headers();
		myHeaders.append('Authorization', 'abc123');

		fetch('/files/cloud', {
			method: 'POST',
			body: formData,
			headers: myHeaders
		})
			.then(resp => resp.json())
			.then(resp => {
				let successMessage = `${resp.msg}. Url to image: ${resp.urlToImage}`;
				alert(successMessage);
			})
			.catch(err => console.error(err));
	};

	return (
		<div>
			<h3>File uploadish</h3>
			<form action="" encType="multipart/form-data" method="POST">
				<input type="file" name="photo" id="file" />
				<button onClick={e => fileUpload(e)}>Send</button>
			</form>
			<br />
			<br />
			<br />
			<h3>LADDA UPP TILL MOLNET</h3>
			<form action="" encType="multipart/form-data" method="POST">
				<input type="file" name="photo" id="cloud-file" />
				<button onClick={e => uploadToCloud(e)}>Send</button>
			</form>
		</div>
	);
};

export default App;
